
import { Routes, Route,BrowserRouter, HashRouter, useNavigate, RouterProvider, createHashRouter, createBrowserRouter } from "react-router-dom";
import Home from "./pages/Home";
import ProductList from "./pages/ProductList";
import { useEffect } from "react";

console.log('version1')

const router = createBrowserRouter([
  {
    path: "/react.github.io",
    element: <Home />,
  },
  {
    path: "/react.github.io/products",
    element: <ProductList />,
  },{
    path: "/products",
    element: <ProductList />,
  }
],{
  basename: "/react.github.io"
});

function App() {



  return (
    
    <RouterProvider router={router} />

    
  );
}

export default App;
